   font-size: 16px;
   font-family: Verdana, Arial, sans-serif, Georgia, Garamond, serif;
   --tab-color: aliceblue;
   --content-background-color: #d9f6ff;
   --minimum-width: 401px;
   --content-container-width:max(60vw, var(--minimum-width));
}

html, body {
   height: 100%;
}

body {
   margin: 0;
   background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
   background-attachment: fixed;
   background-size: contain;
}

header {
   height: 100px;
   display: grid;
   justify-content: center;
   min-width: var(--minimum-width);
}

nav {
   display: grid;
   grid-template-columns: repeat(3, minmax(88px, 1fr));
   align-items: end;
}

button {
   background-color: var(--tab-color);
   height: 50px;
   border-bottom: none;
   border-top-left-radius: 10px;
   border-top-right-radius: 10px;
   font-size: 1rem;
   padding: 0;
   cursor: pointer;
   border: none;
   position: relative;
}

button::before {
   content: "";
   position: absolute;
   bottom: 0;
   left: -19px;
   height: 20px;
   width: 20px;
   pointer-events: none;
}

button::after {
   content: "";
   position: absolute;
   bottom: 0;
   right: -20px;
   height: 20px;
   width: 20px;
   pointer-events: none;
}

button:hover {
   background-color: #89ddf8;
}

#home::before,
#home::after {
   background-color: transparent;
}

#menu::before,
#menu::after {
   background-color: transparent;
}

#about::before,
#about::after {
   background-color: transparent;
}

.tab-curve {
   position: absolute;
   box-sizing: border-box;
   border-style: solid;
   border-width: 20px;
   border-radius: 50%;
   pointer-events: none;
}

.home-left-circle {
   border-color: transparent;
   transform: rotate(45deg);
}

.home-right-circle {
   border-color: transparent;
   transform: rotate(135deg);

}

.menu-left-circle {
   border-color: transparent;
   transform: rotate(45deg);
}

.menu-right-circle {
   border-color: transparent;
   transform: rotate(45deg);
}

.about-left-circle {
   border-color: transparent;
   transform: rotate(45deg);
}

.about-right-circle {
   border-color: transparent;
   transform: rotate(45deg);
}

#home.selected-home::before,
#home.selected-home::after,
#menu.selected-menu::before,
#menu.selected-menu::after,
#about.selected-about::before,
#about.selected-about::after {
   background-color: var(--content-background-color);
}

#home.selected-home::after,
#menu.selected-menu::after,
#about.selected-about::after {
   z-index: 1;
}

#content {
   height: fit-content;
   width: var(--content-container-width);
   min-width: var(--minimum-width);
   margin-left: max(calc(calc(100vw - var(--content-container-width)) / 2), 0px);
   background-color: var(--content-background-color);
   border-radius: 20px;
   margin-bottom: 5vh;
   line-height: 1.6;
   padding: 20px 25px;
}

.home-header {
   text-align: center;
   color: var(--content-background-color);
   margin: 0;
   font-size: 5rem;
   font-family: cursive;
   background-image: url(${___CSS_LOADER_URL_REPLACEMENT_1___});
   height: 300px;
   border-radius: 20px;
   background-position: center;
   background-repeat: no-repeat;
   box-shadow: 0px 10px 20px 0px #434242;
   overflow: hidden;
   max-width: 1000px;
}

.home-intro {
   margin-top: 30px;
}

.home-review {
   display: block;
   font-size: 0.8rem;
}

.home-reviewer-name {
   display: block;
   font-size: 1rem;
   margin-top: 10px;
}

ul {
   padding: 0;
   line-height: 2;
}

li {
   list-style: none;
}

img {
   height: auto;
   width: 300px;
   max-height: 350px;
   border-radius: 20px;
}

.food-container {
   display: grid;
   justify-items: center;
   position: relative;
}

.food-container::after {
   content: '';
   border: 1px solid black;
   width: calc(var(--content-container-width) * 0.7);
   position: relative;
   bottom: 20px;
   min-width: 400px;
}

.food-container:last-child::after {
   border: none;
}

.food-name {
   font-family: 'Comic Sans MS', cursive;
   font-size: 1.6rem;
}

.food-description {
   max-width: 450px;
}

.food-price {
   margin-bottom: 45px;
   width: calc(var(--content-container-width) - 25px);
   max-width: 450px;
   text-align: end;
   font-family: Luminari, fantasy;
}

.about-header {
   background: url(${___CSS_LOADER_URL_REPLACEMENT_2___});
   color: var(--content-background-color);
   background-repeat: no-repeat;
   background-size: cover;
   height: 300px;
   margin: 0px;
   border-radius: 20px 20px 0 0;
   padding-left: 15px;
}

.letter {
   background-color: white;
   padding-bottom: 15px;
}

.letter-header {
   display: grid;
   grid-template-columns: 150px 1fr;
   grid-template-areas: "pic name"
                        "pic title";
   align-items: end;
}

.ceo-pic {
   background: url(${___CSS_LOADER_URL_REPLACEMENT_3___});
   width: 150px;
   height: 150px;
   background-size: cover;
   background-position-x: -50px;
   background-position-y: 20px;
   background-repeat: no-repeat;
   clip-path: circle(35% at 50% 50%);
   grid-area: pic;
}

.ceo-name {
   grid-area: name;
   font-size: 1.1rem;
}

.ceo-title {
   grid-area: title;
   align-self: baseline;
   font-size: 0.8rem;
}

.letter > p {
   padding: 0 10px 0 20px;
   font-size: 0.9rem;
}
.list-image-layout {
   display: flex;
   flex-wrap: wrap;
   justify-content: space-between;
